/* Customize your main colors in :root variables */
:root {
  --main-background-color: #000000;
  --card-background-color: #000000;
  --card-background-lighter-color: #1c2018;
  --main-text-color:#ffffff;
  --title-text-color:#00aa2a;
}

body{
  background-color:var(--main-background-color);
}

h1, p{
  color:#00aa2a;
}


h3{
font-weight: normal;
}

.MuiFab-extended{
  height: auto;
  padding-right: 0 !important;
}

.MuiFab-label{
  font-weight: bold;
}

.MuiPaper-root{
  background-color: var(--card-background-color) !important;
  }

.MuiButton-containedPrimary:not(.MuiButton-fullWidth){
  border-radius: 24px !important;
  padding: 6px 16px;
}

.MuiButton-label{
  font-size: 1.2em;
}

.MuiLinearProgress-colorPrimary{
  background-color:#1c2018 !important;
}

.MuiLinearProgress-barColorPrimary{
  background-color:#00aa2a !important;
}
html, body {
  height: 100%;
}
body {
  margin: 0;
}

#canvas {
  width: 100%;
  height: 100%;
}